import React, { useState } from 'react';

const VoiceBox2 = () => {
  const [text, setText] = useState('');
  const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;

  if (!SpeechRecognition) {
    alert('Web Speech API در این مرورگر پشتیبانی نمی‌شود.');
  } else {
    const recognition = new SpeechRecognition();
    recognition.lang = 'en-US';
    recognition.interimResults = true;

    recognition.onstart = () => console.log('شروع ضبط صدا');
    recognition.onresult = (event) => {
      const transcript = Array.from(event.results)
        .map(result => result[0].transcript)
        .join('');
      setText(text + ' ' + transcript);
      console.log('متن تشخیص داده‌شده:', transcript);
    };

    recognition.onerror = (event) => console.error('خطا:', event.error);

    recognition.start();
  }


  return (
    <div>
      <p>{text}</p>
      test2ss
    </div>
  );
};

export default VoiceBox2;